<template>
    <div id="mealRedpacket-order">
        <public-header title="套餐红包购买记录"></public-header>
        <div class="content">
			<div class="wrap-search wrap-search-input">
				<div class="tab-group flex-lr border-1px-b">
					<div class="tab-item" :class="{active: filter.items.type == 'exchangeRedpacket'}" @click="onToggleType('exchangeRedpacket')">红包套餐</div>
					<div class="tab-item" :class="{active: filter.items.type == 'mealRedpacket'}" @click="onToggleType('mealRedpacket')">红包套餐plus</div>
				</div>
				<van-search placeholder="请输入用户姓名" v-model="filter.items.keyword" />
			</div>
			<van-pull-refresh v-model="isRefresh" @refresh="onPullDownRefresh()">
				<van-list
					v-model="records.loading"
					:finished="records.finished"
					@load="onLoad"
					:offset="100"
					:immediate-check="false"
					v-if="!records.empty"
			 		class="redpacket-list"
				>
					<div class="redpacket-item margin-10-b" v-for="(item, index) in records.data" :key="item.id">
						<div class="item-title flex-lr">
							<div class="title flex">
								<div class="title-img">
									<img :src="item.avatar" alt="">
								</div>
								<div class="title-name">{{item.nickname}}</div>
								<div class="c-gray font-15">UID: {{item.uid}}</div>
							</div>
							<div class="itag" :class="{'itag-primary': item.type == 'exchangeRedpacket'}" v-if="item.type == 'exchangeRedpacket'">红包套餐</div>
							<div class="itag" :class="{'itag-info': item.type == 'mealRedpacket'}" v-else-if="item.type == 'mealRedpacket'">红包套餐plus</div>
						</div>
						<div class="order-detail border-1px-t">
							<div class="order-info flex">
								<div>购买费用：</div>
								<div class="c-danger">¥{{item.final_fee}} <div class="gift-type itag itag-danger">{{item.pay_type_cn}}</div></div>
							</div>
							<div class="order-info flex">
								<div>购买时间：</div>
								<div>{{item.addtime_cn}}</div>
							</div>
							<div class="order-info flex">
								<div>订单号：</div>
								<div>{{item.order_sn}}</div>
							</div>
						</div>
						<div class="border-1px-t btn-group">
							<router-link tag="div" class="btn-item bg-default" :to="util.getUrl({path: '/pages/plugin/mealRedpacket/list', query:{id: item.id}})">查看红包信息</router-link>
						</div>
					</div>
					<div class="loaded" v-if="records.finished">
						<div class="loaded-tips">没有更多了</div>
					</div>
				</van-list>
				<div class="no-data" v-else>
					<img src="static/img/order_no_con.png" alt="">
					<p>没有符合条件的数据!</p>
				</div>
			</van-pull-refresh>
        </div>
		<iloading v-if="showPreLoading"></iloading>
    </div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getRecords} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				records:{
					page: 1,
					psize: 15,
					loading: false,
					finished: false,
					empty: false,
					data: []
				},
				filter: {
					items: {
						type: 'exchangeRedpacket',
					}
				},
				isRefresh: false,
				showPreLoading: true,
			}
		},
		components: {
			publicHeader,
		},
		methods: {
			onLoad(force) {
				var that = this;
				getRecords({
					vue: that,
					force: force,
					url: 'plateform/mealRedpacket/order',
				});
			},
			onToggleType: function(value) {
				if(this.filter.items.type == value) {
					return;
				}
				this.filter.items.type = value;
			},
			onPullDownRefresh() {
				this.onLoad(true);
			},
		},
		mounted(){
			this.onLoad();
		},
		watch: {
			filter: {
				handler(newValue, oldValue) {
					this.onLoad(true)
				},
				deep: true
			},
		}
	}
</script>

<style>
    #mealRedpacket-order .content .redpacket-item{
        margin-bottom: 10px;
        background-color: #fff;
    }
    #mealRedpacket-order .content .redpacket-item .item-title{
        padding: 10px;
    }
    #mealRedpacket-order .content .redpacket-item .item-title .title-img{
        width: 35px;
        height: 35px;
    }
    #mealRedpacket-order .content .redpacket-item .item-title .title-img img{
        width: 100%;
        height: 100%;
        border-radius: 100%;
    }
    #mealRedpacket-order .content .redpacket-item .item-title .title-name{
        font-size: 16px;
        font-weight: bold;
        padding: 0 10px;
    }
    #mealRedpacket-order .content .redpacket-item .order-detail{
        padding: 10px;
    }
    #mealRedpacket-order .content .redpacket-item .order-detail .order-info{
        font-size: 14px;
        color: #7a7a7a;
        padding-bottom: 8px;
    }
    #mealRedpacket-order .content .redpacket-item .order-detail .order-info:last-child{
        padding-bottom: 0px;
    }
    #mealRedpacket-order .content .redpacket-item .order-detail .order-info .gift-type{
        font-size: 12px;
        padding: 2px 4px;
    }
    #mealRedpacket-order .content .redpacket-item .btn-group{
        display: flex;
        flex-wrap: nowrap;
        justify-content: flex-end;
        align-items: center;
        width: 100%;
        padding: 10px;
    }
    #mealRedpacket-order .content .redpacket-item .btn-group .btn-item{
        min-width: 60px;
        margin-left: 10px;
        text-align: center;
        padding: 10px 8px;
        border: 1px solid #e5e5e5;
        border-radius: 3px;
        font-size: 14px;
    }
</style>


